
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import Zoom from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css'
import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/styles.css';
import {useState} from 'react'; 
import 'react-inner-image-zoom/lib/InnerImageZoom/styles.css';
import InnerImageZoom from 'react-inner-image-zoom';
import { Item, AppContainer, ExtraInfo, Code } from "./components";
import Carousel from "./Carousel";
function Home() {

  const[swipe,setSwipe] = useState(true)
  
  function countTouches(event){
	  console.log(event.touches.length)
	  
	  
	  if(event.touches.length==2){
		  setSwipe(false)
		  
	  }
	  else{
		  setSwipe(true)
		  
	  }
	  
  }
  return (
    
        <>
        
    <AwesomeSlider fillParent={true} mobileTouch={swipe}>
            <div>
                <img onTouchStart={(e)=>countTouches(e)} onTouchEnd={(e)=>countTouches(e)} src="https://i.ibb.co/zRdZKKq/Matsya-Menu-AC-F-page-001.jpg" />
            </div>
            <div>
                <img onTouchStart={(e)=>countTouches(e)} onTouchEnd={(e)=>countTouches(e)} src="https://i.ibb.co/QPSMN54/Matsya-Menu-AC-F-page-002.jpg" />
            </div>
            <div>
                <img onTouchStart={(e)=>countTouches(e)} onTouchEnd={(e)=>countTouches(e)} src="https://i.ibb.co/Jp14XMS/Matsya-Menu-AC-F-page-003.jpg" />
            </div>
            <div>
                <img onTouchStart={(e)=>countTouches(e)} onTouchEnd={(e)=>countTouches(e)} src="https://i.ibb.co/8c3hpML/Matsya-Menu-AC-F-page-004.jpg" />
            </div>
            <div>
                <img onTouchStart={(e)=>countTouches(e)} onTouchEnd={(e)=>countTouches(e)} src="https://i.ibb.co/2FwBbCL/Matsya-Menu-AC-F-page-005.jpg" />
            </div>
            <div>
                <img onTouchStart={(e)=>countTouches(e)} onTouchEnd={(e)=>countTouches(e)} src="https://i.ibb.co/C6nzjYs/Matsya-Menu-AC-F-page-006.jpg" />
            </div>
            <div>
                <img onTouchStart={(e)=>countTouches(e)} onTouchEnd={(e)=>countTouches(e)} src="https://i.ibb.co/NLdFVML/Matsya-Menu-AC-F-page-007.jpg" />
            </div>
            
            <div>
                <img onTouchStart={(e)=>countTouches(e)} onTouchEnd={(e)=>countTouches(e)} src="https://i.ibb.co/1s1FbN9/Matsya-Menu-AC-F-page-008.jpg" />
            </div>
        </AwesomeSlider>
    </>

  );
}

export default Home;
